      list-style: none;
      line-height: 40px;
}
.com-contain .com-menu a {
      text-decoration: none;
      display: block;
      padding: 0 10px;
      color: #5e5e5e;
}
.com-contain .com-menu a.router-link-active {
        background: #ccc;
        color: #000 !important;
}
.com-contain .com-view {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}



